import React, {useContext, useState, useEffect, useRef} from 'react'
import {makeStyles} from '@material-ui/core'
import {useParams} from 'react-router-dom'
import * as topojson from "topojson-client";

import {MapContainer, GeoJSON, TileLayer, Tooltip, Marker, SVGOverlay, Popup, LayerGroup} from 'react-leaflet'

import ZoomOutMapIcon from '@material-ui/icons/ZoomOutMap'

import { MapContext } from './mapContext';
import { DataContext } from '../../dataContext'

import MyTooltip from './Tooltip'
import zIndex from '@material-ui/core/styles/zIndex'

const styles = makeStyles({
    mapRoot: {
        flexGrow: 1, 
        display: 'flex', 
        flexDirection: 'column',
        position: 'relative'
    },
    declarationDiv: {
        position: 'absolute', 
        width: '100%',
        zIndex: 100,
        top: 0,
        padding: '5px 0px',
        color: 'white', 
        textShadow: '1px 1px black', 
        fontWeight: 600,
        textAlign: 'center'
    },
    resetButton: {
        display: 'flex',
        boxShadow: '0 1px 4px rgba(0,0,0,0.65)',
        height: 30,
        width: 30,
        fontSize: '1.7rem !important',
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '4px',
        background: '#FFFFFF',
        cursor: 'pointer',  
        color: 'black'  
    },  
    loading: {
        position: 'absolute', 
        width: '100%',
        height: '100%',
        display: 'flex', 
        alignItems: 'center', 
        justifyContent: 'center',
        zIndex: 500
    }
})

const defaultStyle={
    weight: 0.6,
    fillOpacity: 0.8,
    color: 'black',
}

const selectedStyle = {
    weight: 0.8,
    fillOpacity: 0.9
}

const Map = ({showBanner}) => {
    const classes = styles()
    const {state, dispatch, geoJSON} = useContext(MapContext)
    const {data} = useContext(DataContext)
    const [declaration, setDeclaration] = useState(null)

    const geoRef = useRef(null)
    const selectedRef = useRef(null)
    const tooltipRef = useRef(null)

    useEffect(()=> {
        if (data.declaration.overallResult.partyName) {
            const text = data.declaration.overallResult.partyName + " " + data.declaration.overallResult.resultText;
            let color;
            let party = data.party.partyResults.filter(party=>{
                return party.nameShort === data.declaration.overallResult.partyNameShort
            })[0]
            if (party) {
                color = party.color
            } else {
                color = '#C0C0C0'
            }
            setDeclaration({text, color})
        }
    }, [data])

    // useEffect(() => {
    //     if (state.map) {
    //         if (state.selectedProvince && state.provinceZoom) {
    //             let prov = state.provinceData.prov.filter(province=> province.name === state.selectedProvince)[0]
    //             setTimeout(() => {
    //                 state.map.fitBounds(prov.coordinates)                    
    //             }, 500);
    //         } 
    //         else if (!state.selectedProvince) {
    //             state.map.fitBounds([[83.1355, -52.619], [41.711, -141.028]])
    //         } 
    //     }
    // }, [state.selectedProvince])

    useEffect(()=> {
        try {
            if (state.map)  {
                if (state.selectedRiding) {
                    let getGeo=''
                    geoRef.current.eachLayer(layer=>{
                        if (layer.options.name.toLowerCase() === state.selectedRiding.toLowerCase()) {
                            getGeo = layer
                        }
                    })
                    if (getGeo.getBounds()) {
                        getGeo.setStyle(selectedStyle)
                        selectedRef.current = getGeo
                        state.map.fitBounds(getGeo.getBounds())
                    }
                }
            }    
        } catch(e) {
            console.log(e)
        }
    }, [state.selectedRiding])

    const handleSetMap = (e) => {
        // e.options.minZoom = e.getZoom()
        dispatch({type: 'SET_MAP', map: e})
    }

    const resetBounds = () => {
        if (selectedRef.current) {
            selectedRef.current.setStyle(defaultStyle)
            selectedRef.current = null
        }
        state.map.fitBounds(data.config.initBounds)
        dispatch({type: 'RESET'})
    }


    const handleFill = (ridingName) => {
        
        let fillColor = 'lightgrey'
        try {
            let ridingResults = data.full.data.filter(ed=>{
                return ed.name.toLowerCase().trim() === ridingName.toLowerCase().trim()
            })[0]
            
            let party = data.party.partyResults.filter(party=>{
                return party.nameShort === ridingResults.results[0].partyCode
            })[0]
            if (party) {
                if (ridingResults.results[0].votes) {
                    fillColor = party.color
                } else {
                    fillColor = '#C0C0C0'
                }
            } else {
                fillColor = '#C0C0C0'
            }
            console.log(ridingResults.results[0].partyCode)
            if (ridingResults.results[0].partyCode === 'PC') {
                console.log('party is PC')
                fillColor = 'rgb(0, 51, 153)'
            }
            return {results: ridingResults, color: fillColor}
        } catch (e) {
            console.log('error fill', e)
            return  {results: null, color: 'lightgrey'}
        }
    }

    function resetFeature(e) {
        if (selectedRef.current) {
            if (e.layer.feature.properties.Name === selectedRef.current.options.name) {
                return;
            }
        } 
        e.layer.setStyle(defaultStyle)
    }

    const handleClick = (e) => {
        try {
            console.log(e)
            if (selectedRef.current) {
                console.log('tooltip', selectedRef.current.options.name, e.layer.options.name)

                if (selectedRef.current.options.name !== e.layer.options.name) {
                    selectedRef.current.setStyle(defaultStyle)
                }
            }

            var layer = e.layer
            selectedRef.current = e.layer;
            var layerBounds = e.layer.getBounds();
            state.map.fitBounds(layerBounds)
            setTimeout(()=> {
                dispatch({type: 'CLICK_RIDING', riding: layer.feature.properties.Name})
            },500)

        } catch(e) {
            console.log('error click', e)
        }
        
    }

    return (
        <div className={classes.mapRoot} >
            {(declaration && showBanner) && <div className={classes.declarationDiv} style={{background: declaration.color}}>{declaration.text}</div>}
            <MapContainer 
                bounds={data.config.initBounds ? data.config.initBounds :[[83.1355, -52.619], [41.711, -141.028]]}
                zoomSnap={0.25}
                zoomDelta={0.25}
                whenCreated={handleSetMap}
                // maxZoom={13}
                >
                <TileLayer
                    attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url=""
                    />
                    <LayerGroup ref={geoRef}>
                    {geoJSON ? geoJSON.features.map((geo,i)=> {
                        // console.log(geo.properties.Name)
                        const getResults = handleFill(geo.properties.Name )
                        const generateKey = () => {
                            // return geo.properties.Name
                            return `${geo.properties.Name}-${getResults.color}`
                        }
                        return <GeoJSON
                            data={geo}
                            // key={generateKey()}
                            name={geo.properties.Name}
                            style={()=>{return ({
                                weight: 0.6,
                                fillOpacity: 0.8,
                                color: 'black',
                                fillColor: getResults.color
                                })}
                            }
                            eventHandlers={{
                                mouseover: (e)=>e.layer.setStyle(selectedStyle),
                                mouseout: resetFeature,
                                click: handleClick,
                            }}
                        >
                            <Tooltip 
                                key={`${data.counter}-${i}`}
                                sticky 
                                ref={tooltipRef}
                                >
                                    <MyTooltip results={getResults.results} color={getResults.color} riding={geo.properties.Name}/>
                            </Tooltip>
                        </GeoJSON>
                    }): <div className={classes.loading}>
                           <div className="lds-ring"><div></div><div></div><div></div><div></div></div> 
                        </div>}
                    </LayerGroup>
                <div style={{top: 90}} className={'leaflet-top leaflet-left'}>
                    <div  className='leaflet-control'>
                        {/* <button onClick={()=>console.log(state.map.getBounds())}>get bounds</button> */}
               
                        <a id="zoomOut" 
                            style={{color: 'black !important', border: '2px solid rgba(0,0,0,0.25)'}} 
                            className={`leaflet-control-zoom leaflet-bar ${classes.resetButton}`} 
                            onClick={resetBounds}
                            >
                            <ZoomOutMapIcon />
                        </a>
                    </div>
                </div>
            </MapContainer>
        </div>
    )
}

export default Map;