import React, {useContext, useState, useEffect} from 'react'
import {IconButton, Paper, makeStyles, TextField} from '@material-ui/core'

import {
    useParams
} from 'react-router-dom'
import { CSSTransition } from 'react-transition-group'

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore'
import SearchIcon from '@material-ui/icons/Search';

import {Autocomplete} from '@material-ui/lab'

import AccordionBody from './AccordionBody'

import {MapContext} from './mapContext'
import {DataContext} from '../../dataContext'

const styles = makeStyles({
    root: {
        display: 'flex',
        height: '100%',
        flexDirection: 'column',
        overflow: 'hidden'
    }, 
    title: {
        backgroundColor: '#9e9e9e',
        boxShadow: '-1px 1px 2px 0px black',
        zIndex: 402,
        fontSize: 26,
        fontWeight: 600,
        color: 'white',
        maxHeight: 80,
        display: 'flex', 
        alignItems: 'center', 
        textAlign: 'center',
        padding: '10px 5px',
        justifyContent: 'center'
    },

    results: {
        backgroundColor: 'white',
        flexGrow: 1,
        overflow: 'hidden',
        height: '100%'
    },
    accordionBody: {
        display: 'flex', 
        flexDirection: 'column'
    },
    resultsSummary: {
        display: 'flex', 
        justifyContent: 'space-between',
        alignItems: 'center', 
        fontSize: 14,
        fontWeight: 600,
        '& #votes': {
            fontSize: 12
        }
    },
    backButton: {
        minHeight: '48px',
        display: 'flex', 
        alignItems: 'center', 
        paddingRight: 2, 
    },
    autoComplete: {
        width: '100%',
        height: 65,
        display: 'flex',
        // position: 'sticky', 
        // top: 0,
        paddingTop: 6,
        backgroundColor: 'white', 
        zIndex: 100,
        alignItems: 'center',
        '& .MuiAutocomplete-root': {
            width: '95% !important',
            margin: '6px 6px 6px 6px',
            flexGrow: 1
        },
        '& .MuiOutlinedInput-root': {
            // padding: '3px !important'
        }, 
        '& .MuiFormLabel-root': {
            lineHeight: 1
        },
    }
})

const Sidebar = (props) => {

    const {state, dispatch} = useContext(MapContext)
    const {data} = useContext(DataContext)

    const [expanded, setExpanded] = useState('')
    const classes = styles();

    useEffect(() => {
        setExpanded(state.selectedRiding)
        return ()=> {
            setExpanded('')
        }
        
    }, [state.selectedRiding])

    // useEffect(()=> {
    //     if (state.menu === 'provinces') {
    //         setOffset(44)
    //     } else if (state.menu === 'ridings' && !state.selectedRiding) {
    //         setOffset(90)
    //     }
    // }, [state.menu])

    useEffect(()=> {
        if (expanded) {
            setTimeout(()=> {
                console.log(state.selectedRiding)
                var scrollingDiv = document.getElementById("ridingMenu")
                let menuel = document.getElementById(state.selectedRiding);
                if (menuel) {
                    scrollingDiv.scrollTop = menuel.offsetTop
                    window.scrollTo(0,0)
                }
                console.log(scrollingDiv, menuel)
                
            }, 200)
        } else {
            var scrollingDiv = document.getElementById("ridingMenu")
            scrollingDiv.scrollTop = 0
        }
    }, [expanded])

    const handleAccordion = (riding) => {
        if (expanded === riding) {
            setExpanded('')
        } else 
        setExpanded(riding)
        dispatch({type: 'CLICK_RIDING', riding: riding, province: state.selectedProvince})
    }

    const handleSearch = (newSearch) => {
        if (newSearch) {
            dispatch({type: 'CLICK_RIDING', riding: newSearch.name})            
        }

        // if( /Android|webOS|iPhone|iPad|Mac|Macintosh|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
        // }
    }

    return (
        <div className={classes.root}>
            <div id="sidebarTitle" className={classes.title}>
                <div >
                    {state.menu === "provinces" ? "Select a Province to filter Ridings" : "Select a riding to view results"}
                </div>
            </div>
            <div className={classes.results}>
                <div className="menu">
                    <Paper key="autocomplete" className={classes.autoComplete}>
                        <Autocomplete
                            id="combo-box-demo"
                            onChange={(event, newValue) => {
                                handleSearch(newValue)
                                }}
                            options={data.full.data.sort((a, b)=> {
                                if (a.name > b.name) {
                                    return 1
                                } else return -1
                            })}
                            getOptionLabel={(option) => option.name}
                            style={{ width: 300 }}
                            renderInput={(params) => <TextField id="autocompleteTextfield" disabled {...params} label={`Search Ridings`} variant="outlined" />}
                            />
                    </Paper>
                    <div  className="scrollingDiv ridingMenu" id="ridingMenu">
                        {data.full.data.sort((a, b)=> {
                            if (a.name > b.name) {
                                return 1
                            } else return -1
                        })
                        .map(contest=>{
                            return (
                                <Accordion key={contest.id} id={contest.name} expanded={expanded === contest.name} onChange={()=>handleAccordion(contest.name)}>
                                    <AccordionSummary
                                        expandIcon={<ExpandMoreIcon />}
                                        aria-controls="panel1bh-content"
                                        id="panel1bh-header"
                                    >
                                        {contest.name}
                                    </AccordionSummary>
                                    {expanded === contest.name ? <AccordionBody id={contest.id} riding={contest.name}/> : <div />}
                                </Accordion>
                            )
                        })}

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Sidebar;

