import React, {useContext, useEffect} from 'react'

import {DataContext} from '../../dataContext'

import Candidate from './Candidate'

const Results = (props) => {
    const {data} = useContext(DataContext)

    const getColor = (candidate) => {
        const partyCode = candidate.partyCode
        let partyInfo = data.party.partyResults.filter(party => {
            return party.nameShort === partyCode
        })[0]
        console.log('results',partyCode,partyInfo)
        if (partyInfo) {
            return partyInfo
        } else if (partyCode === 'PC') {
            return {
                color: "rgb(0, 51, 153)",
                name: "Progressive Conservative",
                nameShort: "PC"
            }
        } else
        return {
            color: "#C0C0C0",
            name: "Other",
            nameShort: "OTH"
        }
    }

    return <div>
        {/* {console.log('here')} */}
        {props.results.results.map((candidate,i)=>{
            const partyData = getColor(candidate)
            return (
                <div key={candidate.id}>
                    <Candidate id={i} candidate={candidate} partyData={partyData}/>
                </div>
            )
        })}
    </div>
}

export default Results